define('team/team_service',['./team'], function (team) {
  return team.factory('teamService', ['$http', 'apiBaseUrl', function ($http, apiBaseUrl) {
    return {
      /**
       * 
       * @param  {String} id id
       * @return {HttpPromise}
       */
      get: function (id) {
        return $http.get(apiBaseUrl + '/teams/' + id);
      },
      /**
       * 
       * @param  {String}   id      id
       * @return {HttpPromise}
       */
      getList: function (id) {
        return $http.get(apiBaseUrl + '/teams/',{
          params:{
            hostType:'company',
            hostId: id
          }
        });
      },
      getGroups: function () {
        return $http.get(apiBaseUrl + '/groups');
      },
      /**
       * 
       * @param  {String} id         id
       * @param  {[type]} updateData 
       * @return {HttpPromise}
       */
      update: function (id, updateData) {
        return $http.put(apiBaseUrl + '/teams/' +id, updateData);
      },
      /**
       * 
       * @param  {Object} postData 
       * @return {HttpPromise}
       */
      create: function (postData) {
        return $http.post(apiBaseUrl + '/teams',postData);
      },
      /**
       * 
       * @param  {String} tid id
       * @return {HttpPromise}
       */
      close: function (tid) {
        return $http.delete(apiBaseUrl + '/teams/' +tid);
      },
      /**
       * 
       * @param  {String} tid id
       * @return {HttpPromise}
       */
      open: function (tid) {
        return $http.post(apiBaseUrl + '/teams/' +tid +'/actions/open');
      }


    }
  }]);
});
